// 960 Grid System ~ Core CSS.
// Learn more ~ http://960.gs/

// Licensed under GPL and MIT.

// Forces backgrounds to span full width,
// even if there is horizontal scrolling.
// Increase this if your layout is wider.

// Note: IE6 works fine without this fix.

body {
  min-width: 960px; }

// Container

.container_12 {
  margin-left: auto;
  margin-right: auto;
  width: 960px; }

// Grid >> Global

.span1,
.span2,
.span3,
.span4,
.span5,
.span6,
.span7,
.span8,
.span9,
.span10,
.span11,
.span12 {
  display: inline;
  float: left;
  margin-left: 10px;
  margin-right: 10px; }

.push_1, .pull_1,
.push_2, .pull_2,
.push_3, .pull_3,
.push_4, .pull_4,
.push_5, .pull_5,
.push_6, .pull_6,
.push_7, .pull_7,
.push_8, .pull_8,
.push_9, .pull_9,
.push_10, .pull_10,
.push_11, .pull_11 {
  position: relative; }

// Grid >> Children (Alpha ~ First, Omega ~ Last)

.alpha {
  margin-left: 0; }

.omega {
  margin-right: 0; }

// Grid >> 12 Columns

.container_12 .span1 {
  width: 60px; }

.container_12 .span2 {
  width: 140px; }

.container_12 .span3 {
  width: 220px; }

.container_12 .span4 {
  width: 300px; }

.container_12 .span5 {
  width: 380px; }

.container_12 .span6 {
  width: 460px; }

.container_12 .span7 {
  width: 540px; }

.container_12 .span8 {
  width: 620px; }

.container_12 .span9 {
  width: 700px; }

.container_12 .span10 {
  width: 780px; }

.container_12 .span11 {
  width: 860px; }

.container_12 .span12 {
  width: 940px; }

